@import url('https://fonts.googleapis.com/css2?family=Cabin:ital@1&family=Dancing+Script:wght@500&family=Yanone+Kaffeesatz:wght@300&display=swap');

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    outline: 0;
    border: 0;
    list-style: none;
    text-decoration: none;
}

:root{
    --color-bg:#1f1f48;
    --color-bg-variant:#2c2c6c;
    --color-primary:#4db5ff;
    --color-primary-variant:rgba(77,181,255,0.4);
    --color-white:#fff;
    --color-light:rgba(255,255,255,0.6);

    --container-width-lg:75%;
    --container-width-md:86%;
    --container-width-sm:90%;
    --Cabin-font-family: 'Cabin', sans-serif;
    --Dancing-font-family: 'Dancing Script', cursive;
    --Yano-font-family: 'Yanone Kaffeesatz', sans-serif;

    --transition-ease:all 400ms ease;
}

html{
    scroll-behavior: smooth;
}

::-webkit-scrollbar{
    display: none;
}
body{
font-family:var(--Yano-font-family) ;
background-color: var(--color-bg);
color: var(--color-white);
line-height: 1.7;
background-image: url(../src/assets/newbg2.avif);
background-repeat: no-repeat;
background-attachment: fixed;
background-size: cover;
}

.container{
    width: var(--container-width-lg);
    margin: 0 auto;
}

h1,h2,h3,h4{
    font-weight: 500;
}

h1{
    font-size: 2.5rem;
    margin-bottom: 2rem;
}
section{
    margin-top: 8rem;
}

section > h2,section>h5{
    text-align: center;
    color: var(--color-light);
}

section>h2{
    color: var(--color-primary);
    margin-bottom: 3rem;
}
.text-light{
    color: var(--color-light);
}
a{
    color: var(--color-primary);
    transition:var(--transition-ease) ;
}
a:hover{
    color: var(--color-white);
}
.btn{
    width: max-content;
    display: inline-block;
    color: var(--color-primary);
    padding: .75rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid var(--color-primary);
    transition: var(--transition-ease);
}

.btn:hover{
    background-color: var(--color-white);
    color: var(--color-bg);
    border-color: transparent;
}

.btn-primary{
    background-color: var(--color-primary);
    color: var(--color-bg);
}

img{
    display: block;
    width: 100%;
    object-fit: cover;
}
/*=======================================MEDIA QUERY MEDIUM DEVICES==================*/

@media screen and (max-width:1024px) {
    .container{
        width: var(--container-width-md);
    }
    section{
        margin-top: 2rem;
    }
}
/*=======================================MEDIA QUERY MEDIUM small==================*/

@media screen and (max-width:600px) {
    .container{
        width: var(--container-width-sm);
    }
    section>h2{
        margin-bottom: 2rem;
    }
}
